@import "https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&display=swap";
html, body {
  overflow: hidden;
}

.background {
  will-change: transform;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  height: 130vh;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: all 1.2s cubic-bezier(.22, .44, 0, 1);
  position: fixed;
  overflow: hidden;
  transform: translateY(30vh);
}

.background:first-child {
  background-color: rgba(0, 0, 0, .3);
  background-image: url("background.cfb1dc66.jpg");
  transform: translateY(-15vh);
}

.background:first-child .content-wrapper {
  transform: translateY(15vh);
}

.background:nth-child(1) {
  z-index: 3;
}

.background:nth-child(1):before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .36);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.content-title {
  color: #fff;
  font-family: Dancing Script, cursive;
  font-size: 70px;
  position: absolute;
  top: 20vh;
  right: 10vw;
}

.content-subtitle {
  color: #fff;
  font-family: Dancing Script, cursive;
  font-size: 40px;
  position: absolute;
  top: 40vh;
  right: 20vw;
}

.content-subtitle-2 {
  color: #fff;
  font-family: Dancing Script, cursive;
  font-size: 34px;
  position: absolute;
  top: 82vh;
  right: 50%;
  transform: translate(50%, -50%);
}

.scroll-down {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  margin-top: 20px;
  font-family: Dancing Script, cursive;
  font-size: 30px;
  display: block;
}

.background.down-scroll {
  transform: translate3d(0, -130vh, 0);
}

.background.down-scroll .content-wrapper {
  transform: translateY(40vh);
}

.background.down-scroll + .background:not(.down-scroll) {
  transform: translate3d(0, -15vh, 0);
}

.background.down-scroll + .background:not(.down-scroll) .content-wrapper {
  transform: translateY(15vh);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

html {
  color: #f7f0f5;
  background: url("magictheme1.535bffbe.jpg") 0 0 / cover;
}

.loader {
  width: 78px;
  height: 78px;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 78px;
  height: 78px;
  border: 3px solid #f6f6f6;
  border-radius: 50%;
  animation: animloader 1s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  animation-delay: .5s;
}

@keyframes animloader {
  0% {
    opacity: 1;
    transform: scale(0);
  }

  100% {
    opacity: 0;
    transform: scale(1);
  }
}

.cat-img {
  height: 350px;
  border-radius: 10px;
  box-shadow: 0 0 0 10px rgba(67, 54, 51, .518);
}

.cat-wrapper {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.cat-title {
  padding: 30px 0;
}

.cat-description {
  letter-spacing: .2px;
  padding: 30px 20px 20px;
  font-size: 22px;
}

.cat-temperament {
  padding: 0 20px 30px;
  font-size: 22px;
}

.description-wrapper {
  width: 100%;
  background-color: rgba(222, 203, 183, .75);
  margin-top: 20px;
}

.shadow {
  width: 640px;
  background-color: rgba(128, 128, 128, .643);
  border-radius: 20px;
  margin: 20px auto;
}

.ss-main {
  width: 500px;
  margin: 20px auto 0;
}

.fa-arrow-down {
  font-size: 30px;
  animation: moveDown 1.5s linear infinite;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes moveDown {
  0% {
    top: 99vh;
  }

  50% {
    top: calc(99vh - 80px);
  }

  100% {
    top: 99vh;
  }
}

